.expense-item{
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0.5rem;
    margin: 1rem 0;
    background-color: #eadca6;
    border-radius: 12px;
    box-shadow: 0 1px 8px rgba(0 ,0 , 0,0.25);
    gap: 20px;
}

.expense-item__description{
    display: flex;
    flex-direction: column;
    gap: 1rem;
    align-items: flex-end;
    flex-flow: column-reverse;
    justify-content: flex-start;
    flex: 1;
}

.expense-item__price{
 font-size: 1rem;
 font-weight: bold;
 color: white;
 background-color: #ca3e47;
 border: 1px solid white;
 padding: 0.5rem;
 border-radius: 12px;
}

@media (min-width: 600px){
    .expense-item__description{
        flex-direction: row;
        align-items: center;
        justify-content: flex-start;
        flex: 1;
    }


.expense-item__description h2{
    font-size: 1.25rem;
}

.expense-item__price{
    font-size: 1.25rem;
    padding: 0.5rem 1.5rem;
}

}

.expense-date{

    display: flex;
    flex-direction: column;
    width: 5.4rem;
    height: 5.5rem;
    border: 1px solid #ececec;
    background-color: #bb574e;
    color: white;
    border-radius: 12px;
    align-items: center;
    justify-content: center;
}

.expense-date__month{
   font-size: 0.75rem;
   font-weight: bold;
}

.expense-date__year{
 font-size: 0.75rem;
}

.expense-date__day{
 font-size: 0.75rem;
 font-weight: bold;
}